import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { IssueDeliveryGoodsComponent } from './issue-delivery-goods.component';
import { AddComponent } from './add/add.component';
import { DetailComponent } from './detail/detail.component';
import { DetailSendoComponent } from './detail-sendo/detail-sendo.component';
import { RouterModule, Routes } from '@angular/router';
import { NgZorroAntModule } from 'src/app/ng-zorro-ant.module';
import { TranslateModule } from '@ngx-translate/core';
import { IonicModule } from '@ionic/angular';
import { FormsModule } from '@angular/forms'; 
import { SearchModule } from 'src/app/sales/components/search/search.module';
import { StaffComponent } from './add/staff/staff.component';
import { BillType1SendoModule } from 'src/app/sales/components/bill-type/bill-type-1/sendo/bill-type1-sendo.module';
import { EditComponent } from './edit/edit.component';
const routes: Routes = [
  {
    path: "",
    component: IssueDeliveryGoodsComponent
  },
  {
    path: "add",
    component: AddComponent
  },
  {
    path: "edit",
    component: EditComponent
  },
  {
    path: "detail",
    component: DetailComponent
  },
  {
    path: "detail-sendo",
    component: DetailSendoComponent
  }
]

@NgModule({
  declarations: [
    IssueDeliveryGoodsComponent, AddComponent, DetailComponent, DetailSendoComponent, StaffComponent, EditComponent
  ],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    NgZorroAntModule,
    TranslateModule,
    IonicModule,
    FormsModule,
    BillType1SendoModule
  ]
})
export class IssueDeliveryGoodsModule { }
